  width: 35px;
}

.userImg {
  position: absolute;
  top: -5px;
  left: -4px;
}

.ui.button {
  margin: 0;
  padding: 0;

  letter-spacing: 2px;
  text-transform: uppercase;

  color: #fff;
  background-color: transparent !important;

  font-family: Lato !important;
  font-size: 18px;
  font-weight: 400;
}

.ui.button:focus {
  color: #fff;
}

.ui.button.nav-link {
  position: absolute;
  top: 1.2px;
  left: 0px;

  padding-left: 25px;
}

.ui.button:hover {
  color: #fff !important;
}

.ui.button.logoutButton {
  padding-left: 30px;

  letter-spacing: normal;
  text-transform: none;

  color: #444;
}

.ui.button.logoutButton:hover {
  color: #444 !important;
}

.ui.bottom.right.popup.transition.visible {
  top: 36px;
  right: 20px;

  text-align: right;
}

.logoutImg {
  position: absolute;
  right: 80px;
  bottom: 13px;
}

.userName  {
  margin-bottom: 20px;

  color: #444;

  font-size: 20px;
}

.navigation__link--user {
  flex: 1 1;
}

.navigation__img {
  position: absolute;
  bottom: 0px;
  left: -13px;
}

.navigation__img--chart {
  bottom: 2px;
}

@media screen and (max-width: 640px) {
  .navigation__text {
    display: none;
  }

  .navigation__link--user {
    width: 45px;
    margin-right: 0;
  }

  .navigation__list-item--user {
    margin-right: 18px;
  }

  .ui.bottom.right.popup.transition.visible {
