  border-left: none;
  border-right: none;
  box-shadow: none;
}

.ui.dropdown>.text {
  font-size: 18px;
}

.ui.dropdown .menu>.item {
  font-size: 18px;
}

.filters .ui.selection.dropdown>.delete.icon, .ui.selection.dropdown>.dropdown.icon, .ui.selection.dropdown>.search.icon {
  top: 18px;
}

.ui.default.dropdown:not(.button)>.text, .ui.dropdown:not(.button)>.default.text {
