.pagination {
  position: absolute;
  top: 10px;
  right: 50px;

  display: flex;
  flex-direction: row;

  padding-left: 0;

  text-align: center;

  align-content: center;
}

.pagination__item {
  position: relative;

  min-width: 20px;

  list-style: none;

  color: #2e2e2e;
  border: none;
  background-color: #fff;

  font-size: 17px;
}

.pagination__item--active {
  color: #fff;
  background-color: #444;
}

.pagination__link:hover {
  color: #a299b4;
}

.pagination__item--disabled {
  color: lightgrey;
  background-color: #fff;
}

.pagination__link {
  display: block;

  height: 100%;
  padding-top: 5px;
  padding-right: 9px;
  padding-bottom: 5px;
  padding-left: 9px;

  text-align: center;
  text-decoration: none;

  color: inherit;
}

@media screen and (max-width: 800px) {
  .pagination {
    top: 2px;

  }

  .pagination__link {
    font-size: 16px;
    padding-left: 6px;
    padding-right: 6px;
  }
}

@media screen and (max-width: 640px) {
  .pagination {
      right: 23px;
  }
}
