.page-title {
  position: absolute;
  top: -95px;
  left: 40px;

  margin: 0;

  text-align: left;
  letter-spacing: 2px;
  text-transform: uppercase;

  color: #fff;

  font-family: Lato;
  font-size: 50px;
}

.page-title--vacancy {
  font-size: 38px;
  top: -84px;
}

@media screen and (max-width: 1080px) {
  .page-title {
    top: -60px;

    color: #444;

    font-size: 35px;
  }
  .page-title--vacancy {
    top: -84px;

    color: #fff;

    font-size: 33px;
  }

  .page-title--company {
    top: -95px;

    color: #fff;

    font-size: 50px;
  }
}

@media screen and (max-width: 640px) {
  .page-title {
    top: -40px;

    margin-bottom: 30px;

    font-size: 24px;
  }
}
